html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  color: #151515;
  font-weight: 700;
}

h1 {
  font-size: 72px;
}

h2 {
  font-size: 48px;
  font-weight: 500;
}

h3 {
  font-size: 36px;
}

h4 {
  font-size: 15px;
}

h5 {
  font-size: 13px;
}

p {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: "Jost", sans-serif;
  position: relative;
  background-color: rgba(232, 245, 231, 0.8);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: #151515;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 50px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main__link {
  font-size: 15px;
  display: inline-block;
  transition: 0.3s all;
}

.main__link_white {
  color: #151515;
  transition: 0.3s all;
}

.main__link:after {
  content: '';
  margin-top: 5px;
  display: block;
  width: 100%;
  opacity: 0;
  height: 2px;
  background-color: #151515;
  transition: 0.5s all;
}

.main__link:hover:after {
  content: '';
  margin-top: 5px;
  display: block;
  width: 100%;
  opacity: 1;
  height: 2px;
  background-color: #151515;
}

.btn {
  background-color: #dda73c;
  font-weight: 700;
  padding: 10px 20px;
  border-radius: 25px;
  text-align: center;
  color: #ffffff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  transition: .3s;
}

.btn:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.header {
  padding-top: 39px;
  padding-bottom: 39px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  transform: translateX(-110%);
}

.logo {
  width: 117px;
  height: 22px;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 45px;
}

.nav__link {
  color: #fff;
}

.nav__link_active {
  cursor: text;
}

.nav__link_active:after {
  background-color: #ffffff;
}

.nav__link_active:after {
  opacity: 1;
}

.nav__link:hover:after {
  background-color: #ffffff;
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__item {
  display: inline-block;
  width: 16px;
  height: 16px;
}

.shop-nav__item:not(:last-child) {
  margin-right: 27px;
}

.shop-nav__item_user {
  background: url(../../img/user-icon.png) center center/cover no-repeat;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
}

.shop-nav__link:last-child {
  display: flex;
}

.shop-nav__quantity {
  display: inline-block;
  color: #ffffff;
  font-size: 10px;
  height: 16px;
  line-height: 16px;
  padding: 0 5px;
  background-color: #151515;
  border-radius: 25px;
  margin-left: 3px;
}

.search-form__btn {
  display: inline-block;
  height: 16px;
  width: 16px;
  background: url(../../img/search-icon.png) center center/cover no-repeat;
}

.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: #000000;
  box-shadow: 0px 5px 18px rgba(0, 0, 0, 0.4);
}

.hero__container {
  height: 100%;
}

.hero:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  clip-path: polygon(100% 0, 100% 50%, 100% 100%, 25% 100%, 43% 51%, 25% 0);
  background: #7dcc77;
}

.hero__offer {
  max-width: 630px;
  display: inline-block;
  padding-left: 120px;
}

.hero__procent {
  background-image: linear-gradient(45deg, #ff00e3, #41e7f4, #ecff01, #54d689);
  display: inline-block;
  background-size: 400%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  animation: animate 10s linear infinite;
}

.hero__title {
  color: #ffffff;
}

.hero__descr {
  font-size: 18px;
  color: #ffffff;
  margin-top: 40px;
}

.hero__img {
  position: absolute;
  bottom: 0;
  right: 0;
  max-height: 100%;
}

@keyframes animate {
  0% {
    background-position: 0%;
  }
  25% {
    background-position: 100%;
  }
  50% {
    background-position: 200%;
  }
  75% {
    background-position: 200%;
  }
  100% {
    background-position: 400%;
  }
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.swiper-container-horizontal > .swiper-pagination-bullets {
  left: 180px;
  bottom: 50px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 8px;
  background-color: #fff;
}

.swiper-pagination-bullet-active {
  background-color: #ffffff;
  height: 16px;
  width: 16px;
  transition: 0.3s all;
  position: relative;
  opacity: 0.8;
}

.swiper-pagination-bullet-active:before {
  content: '';
  display: block;
  position: absolute;
  background-color: #000000;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.marketing {
  position: absolute;
  left: 50px;
  bottom: 14%;
  background-color: #ffffff;
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.1);
  padding: 10px;
  border-radius: 5px;
  display: flex;
  font-size: 11px;
  color: #858585;
  z-index: 100;
  transform: translateX(-150vw);
  transition: 0.3s all;
}

.marketing_visible {
  transform: translateX(0);
  transition: 0.3s all;
}

.marketing__img {
  max-width: 70px;
  max-height: 90px;
  margin: auto 0;
}

.marketing__title {
  margin: 8px 0 20px;
}

.marketing__text {
  margin: 5px 30px 5px 10px;
  max-width: 150px;
}

.marketing__price {
  font-size: 10px;
  position: absolute;
  bottom: 10px;
  right: 20px;
  font-weight: 700;
  color: #ffffff;
  background-color: #ca0000;
  padding: 5px 10px;
}

.marketing__btn {
  display: block;
  margin-bottom: auto;
  margin-top: 5px;
}

.banners {
  padding-top: 50px;
  padding-bottom: 50px;
  z-index: 1;
}

.banners__list {
  display: flex;
  justify-content: space-between;
}

.banners__item {
  width: 32%;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.6);
  transition: 0.3s all;
  z-index: 2;
}

.banners__item:hover {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
}

.banners__content {
  min-height: 290px;
  padding: 60px 240px 110px 63px;
}

.banners__content_womens {
  background: url(../../img/woman2.png);
  background-repeat: no-repeat;
  background-position: 305px -25px;
  background-color: #eeaeca;
  background-size: contain;
}

.banners__content_mens {
  background: url(../../img/man2.png) right bottom/cover no-repeat;
  background-color: #151515;
  background-size: contain;
}

.banners__content_mens h3 {
  color: #ffffff;
}

.banners__content_mens a {
  color: #ffffff;
}

.banners__content_mens a:hover:after {
  background-color: #ffffff;
}

.banners__content_all {
  background: url(../../img/woman.png) right bottom/cover no-repeat;
  background-color: #7dcc77;
  background-size: contain;
}

.banners__title {
  margin-bottom: 30px;
}

.main-products {
  padding: 55px 0;
  position: relative;
  z-index: 1;
}

.main-products__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 45px;
}

.main-products__btn {
  font-size: 24px;
  margin: 0 17px;
}

.main-products__btn_active {
  cursor: text;
}

.main-products__btn_active:after {
  opacity: 1;
}

.main-products__btn-center {
  margin-top: 65px;
  text-align: center;
}

.main-products__btn-more {
  background-color: #e86b4a;
  font-weight: 700;
  width: 160px;
  padding: 10px 20px;
  border-radius: 25px;
  text-align: center;
  color: #ffffff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  transition: 0.3s all;
}

.main-products__btn-more:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.products-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: top;
}

.products-grid__item {
  margin-bottom: 30px;
}

.products-grid__item:nth-last-child(-n+5) {
  margin-bottom: 0;
}

.product {
  width: 340px;
  padding: 15px 15px 25px;
  text-align: center;
  position: relative;
  background-color: #ffffff;
}

.product__image {
  margin-bottom: 30px;
  max-width: 340px;
}

.product__image img {
  object-fit: cover;
  max-height: 430px;
}

.product__title {
  text-transform: uppercase;
}

.product__title a:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  transition: 0.3s all;
}

.product__title a:hover:before {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
}

.product__price {
  max-width: 100px;
  font-size: 13px;
  display: block;
  margin-top: 25px;
  margin-left: auto;
  background-color: #ca0000;
}

.shop-banners {
  padding: 55px 0px;
  position: relative;
}

.shop-banners__container {
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.shop-banner {
  position: relative;
  width: 50%;
  padding: 20px 40px 300px;
  display: flex;
  flex-direction: column;
  background-color: #000000;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  z-index: 2;
}

.shop-banner_brands {
  background: url(../../img/banner-1.jpg) center center/cover no-repeat;
}

.shop-banner_run {
  background: url(../../img/banner-2.jpg) right center/cover no-repeat;
}

.shop-banner__title {
  color: #ffffff;
}

.shop-banner__title span {
  color: #feaf2c;
}

.shop-banner__text {
  max-width: 140px;
  margin-top: 20px;
  background-color: #e86b4a;
}

.shop-banner__img {
  position: absolute;
  bottom: 0;
  right: 50px;
  max-height: 100%;
}

.footer {
  padding-top: 55px;
  color: #ffffff;
  background-color: #7dcc77;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.footer:before {
  content: '';
  display: block;
  position: absolute;
  background-color: #000000;
  left: 0;
  bottom: 0;
  width: 700px;
  height: 700px;
  transform: rotate(45deg);
  z-index: -1;
}

.footer__content {
  padding: 0 260px;
}

.footer__title {
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 40px;
}

.footer__item:not(:last-child) {
  margin-bottom: 22px;
}

.footer__link {
  color: #ffffff;
  font-weight: 500;
  font-size: 12px;
}

.footer__link:after {
  background-color: #ffffff;
}

.footer__link:hover:after {
  background-color: #ffffff;
}

.footer__top {
  display: flex;
  justify-content: space-between;
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
  padding: 55px 0;
  border-top: 1px solid #ffffff;
}

.footer__text {
  display: inline-block;
  margin-top: 25px;
}

.list-rights {
  display: flex;
}

.list-rights__item:not(:last-child) {
  margin-right: 45px;
}

.list-rights__link {
  color: #ffffff;
}

.list-rights__link:after {
  background-color: #ffffff;
}

.list-rights__link:hover:after {
  background-color: #ffffff;
}

.social {
  display: flex;
}

.social__item:not(:last-child) {
  margin-right: 30px;
}

.social__link {
  width: 16px;
  height: 16px;
  display: inline-block;
}

.social__img {
  transition: 0.5s all;
}

.social__img:hover {
  transform: rotate(360deg);
}

.subscribe-form {
  width: 650px;
}

.subscribe-form__title {
  color: #ffffff;
  margin-bottom: 40px;
}

.subscribe-form__field {
  display: flex;
}

.subscribe-form__field input {
  width: 100%;
  display: inline-block;
  border: none;
  outline: none;
  background-color: transparent;
  color: #ffffff;
  border-bottom: 2px solid #ffffff;
  padding-right: 40px;
}

.subscribe-form__field input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.subscribe-form__btn {
  display: inline-block;
  margin-left: 30px;
}
