.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: $black-color;
  box-shadow: 0px 5px 18px rgba(0, 0, 0, .4);

  &__container {
    height: 100%;
  }

  &:before {
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    clip-path: polygon(100% 0, 100% 50%, 100% 100%, 25% 100%, 43% 51%, 25% 0);
    background: $green-color;
  }

  &__offer {
    max-width: 630px;
    display: inline-block;
    padding-left: 120px;
  }

  &__procent {
    background-image: $procent-gradient;
    display: inline-block;
    background-size: 400%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    animation: animate 10s linear infinite;
  }

  &__title {
    color: $white-color;
  }

  &__descr {
    font-size: 18px;
    color: $white-color;
    margin-top: 40px;
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: 0;
    max-height: 100%;
  }

}

@keyframes animate {
  0% {
    background-position: 0%;
  }

  25% {
    background-position: 100%;
  }

  50% {
    background-position: 200%;
  }

  75% {
    background-position: 200%;
  }

  100% {
    background-position: 400%;
  }
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;

  & .swiper-slide {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
}

.swiper-container-horizontal>.swiper-pagination-bullets {
  left: 180px;
  bottom: 50px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 8px;
  background-color: #fff;
}

.swiper-pagination-bullet-active {
  background-color: $white-color;
  height: 16px;
  width: 16px;
  transition: 0.3s all;
  position: relative;
  opacity: 0.8;
  &:before {
    content:'';
    display: block;
    position: absolute;
    background-color: $black-color;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}


